<template>
  <div>
    <h2>Logging out..</h2>
  </div>
</template>

<script>
import router from '../../router'

export default {
  data: function() {
    return {}
  },
  created() {
    console.log(this.$store.state.cognitoUser)
    this.$store.state.cognitoUser.signOut()
    this.$store.dispatch('signout', {})
    window.sessionStorage.clear()
    window.localStorage.clear()
    router.push('/login')
  },
}
</script>



